@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
.card-container {
  border: 1px solid;
  width: 100px;
  height: 100px;
  margin: 10px;
  cursor: pointer;
  border-radius: 8px;
}

#board {
  display: flex;
  flex-wrap: wrap;
  background-color: lightgray;
  border: 1px solid black;
  width: 370px;
  margin: 0 auto;
  justify-content: center;
  border-radius: 8px;
}

.App {
  font-family: "Roboto";
  text-align: center;
  box-sizing: content-box;
}

h1 {
  font-weight: 700;
}

h2 {
  font-weight: 400;
}

